import React from "react"
import { StaticQuery, graphql } from "gatsby"
import Image from "gatsby-image"

import { rhythm } from "../utils/typography"
import styles from './bio.module.css'

const Bio = ({showWrittenBy = true}) => {
  return (
    <StaticQuery
      query={bioQuery}
      render={data => {
        const { author } = data.site.siteMetadata
        return (
          <div className={styles.bio_container} style={{marginBottom: rhythm(1.5)}}>
            <Image
              fixed={data.avatar.childImageSharp.fixed}
              alt={author}
              style={{ marginRight: rhythm(1 / 2) }}
              className={styles.bio_image_style}
              imgStyle={{ borderRadius: `50%` }}
            />
            <div style={{ display: 'flex', alignItems: 'center'}}>
              {showWrittenBy ? 'Written by' : null} <b style={{ marginLeft: 10}}>{' '}{author}</b>.
            </div>
          </div>
        )
      }}
    />
  )
}

const bioQuery = graphql`
  query BioQuery {
    avatar: file(absolutePath: { regex: "/profile-pic.jpg/" }) {
      childImageSharp {
        fixed(width: 50, height: 50) {
          ...GatsbyImageSharpFixed
        }
      }
    }
    site {
      siteMetadata {
        author
        social {
          twitter
        }
      }
    }
  }
`

export default Bio
